<template>
	<div class="seller" ref="menuWrapper">
			<div class="titleCenter">
				<div class="titleName">
					<div class="pinjia">
						<p class="title">{{ seller.name }}</p>
						<Star :size="'middle'" :score='seller.score' class="star"></Star>
						<span class="ratingCount">({{seller.ratingCount}})</span>
						<span class="sellCount">月售{{seller.sellCount}}单</span>
					</div>
					<div class="titleR" @click="show">
                        <i class="iconfont icon-xin1" ref="Xin"></i>
                        <p ref="collection">收藏</p>
                    </div>
				</div>
				<div class="price">
					<div class="left">
						<span class="leftName">起送价</span><br>
						<span class="leftPrice">{{seller.minPrice}}</span>
						<span class="yuan">元</span>
					</div>
					<div class="middle">
						<span class="leftName">商家配送</span><br>
						<span class="leftPrice">&nbsp{{seller.deliveryPrice}}</span>
						<span class="yuan">元</span>
					</div>
					<div class="right">
						<span class="leftName">平均配送时间</span><br>
						<span class="leftPrice">&nbsp&nbsp{{seller.deliveryTime}}</span>
						<span class="yuan">元</span>
					</div>
				</div>
				<div class="kong"></div>
				<div class="notice">
					<div class="active">
						<p class="activeText">公告与活动</p>
						<p class="activeContent">{{seller.bulletin}}</p>
					</div>
					<ul class="huodong">
						<li class="huodongli" v-for="(v,index) in  seller.supports">
					    	<img :src="url[index]" class="liimg">
					    	<p class="huodonglitext">{{v.description}}</p>
						</li>
					</ul>
				</div>
				<div class="kong"></div>
				<div class="shijing">
			    	<p class="shijingName">商家实景</p>
		            <div class="shijingImg" ref="shijingWrapper">
		                <ul class="imhul">
		                    <li class="imgli" v-for="(v,index) in seller.pics">
		                        <img :src="v" class="imgI">
		                    </li>
		                </ul>
		            </div>
			    </div>
			    <div class="kong"></div>
			    <div class="sellerInfo">
		            <p class="sellerName">商家信息</p>
		            <ul class="huodong">
		                <li class="huodongli" v-for="(v,index) in  seller.infos">
		                    <p class="huodonglitext">{{v}}</p>
		                </li>
		            </ul>
		        </div>
			</div>
		
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import Star from '../star/star.vue'

	export default {
		data(){
            return {
            	showXin:true,
            	url:[
            	'../../static/imgs/decrease.png',
            	'../../static/imgs/discount.png',
            	'../../static/imgs/special.png',
            	'../../static/imgs/invoice.png',
            	'../../static/imgs/guarantee.png'
            	]
            }
   	   },
   	   created() {
            this.$store.dispatch('getSeller');
            this.$nextTick(() => {
                setTimeout(() => {
                    this._initMenu();
                    this._initShijing();
                }, 100)
            })
        },
		components: {
            Star
        },
		computed: {
            ...mapGetters([
                'seller'
            ])
        },
        methods:{
        	_initMenu() {
                this.menuBS = new BScroll(this.$refs.menuWrapper,{click : true});
            },
            _initShijing(){
              this.shijingWrapper = new BScroll ( this.$refs.shijingWrapper ,{
                    click : true,
                    scrollX: true
                });
           },
           show(){
           	console.log(2);
                if(this.showXin){
                   this.$refs.Xin.style.color="rgb(240,20,20)";
                   this.$refs.collection.innerHTML="已收藏";
                   this.showXin=false;
                }else{
                   this.$refs.Xin.style.color="rgb(147,153,159)";
                   this.$refs.collection.innerHTML="收藏";
                   this.showXin=true;
                } 
            }
        }
	}	
</script>

