		height:8.8rem;
		overflow: hidden;
		.titleCenter{
			.titleName{
				margin: 0.36rem 0.36rem 0 0.36rem;
				position: relative;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
				.title{
					font-size: 0.28rem;
					color: rgb(7, 17, 27);
					line-height: 0.28rem;
					padding-bottom: 0.16rem;
				}
				.pinjia{
					display: inline-block;
					// margin-bottom: 0.36rem;
					.star{
						margin-bottom: 0.36rem;
					}
