						position: absolute;
						top:0.55rem;
						left: 2.5rem;
						font-size: 0.2rem;
						color: rgb(77, 85, 93);
						line-height: 0.2rem;
					}
					.sellCount{
						position: absolute;
						top:0.55rem;
						left: 3.2rem;
						font-size: 0.2rem;
						color: rgb(77, 85, 93);
						line-height: 0.2rem;
					}
				}
				.titleR{
                    position:absolute;
                    top:0;
                    right:0.36rem;
                    width:0.8rem;
                    text-align: center;
                    i{
                        font-size:0.48rem;
                        color:rgb(147,153,159);
                        line-height:0.48rem;

                    }
                    p{
                        font-size:0.2rem;
                        color:rgb(147,153,159);
                        line-height:0.2rem;
                        margin-top:0.08rem;
                    }
                }
			}
			.price{
				// margin: 0.36rem 0.36rem 0 0.36rem;
				height: 1.5rem;
				display: flex;
				border-bottom: 1px solid rgba(7, 17, 27, 0.1);
				.left{
					width: 33%;
					text-align: center;
					// padding-left: 0.56rem;
					padding-top: 0.36rem;
					// border-right: 1px solid rgba(7, 17, 27, 0.1);
					.leftName{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.2rem;
					}
					.leftPrice{
						font-size: 0.48rem;
						font-weight: 200;
						color: rgb(7, 17, 27);
						line-height: 0.48rem;
					}
				}
				.middle{
					width: 33%;
					text-align: center;
					// padding-left: 0.56rem;
					padding-top: 0.36rem;
					// border-right: 1px solid rgba(7, 17, 27, 0.1);
					.leftName{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.2rem;
					}
					.leftPrice{
						font-size: 0.48rem;
						font-weight: 200;
						color: rgb(7, 17, 27);
						line-height: 0.48rem;
					}
				}
				.right{
					width: 33%;
					text-align: center;
					// padding-left: 0.56rem;
					padding-top: 0.36rem;
					.leftName{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.2rem;
					}
					.leftPrice{
						font-size: 0.48rem;
						font-weight: 200;
						color: rgb(7, 17, 27);
						line-height: 0.48rem;
					}
				}
			}
			.kong{
				height: 0.36rem;
				width: 100%;
				background-color: rgba(7, 17, 27, 0.05);
			}
			.notice{
				.active{
					border-top: 1px solid rgba(7, 17, 27, 0.1);
					.activeText{
						font-size: 0.28rem;
						font-weight: 700;
						padding-bottom: 0.16rem;
						padding-top: 0.36rem;
						padding-left: 0.36rem;
					}
					.activeContent{
						font-size: 0.24rem;
						font-weight: 200;
						color: rgb(240, 20, 20);
						line-height: 0.48rem;
						padding-left: 0.6rem;
						padding-right: 0.6rem;
						padding-bottom: 0.32rem;
					}
				}
				.huodong{
					.huodongli{
						height: 0.96rem;
						margin: 0 0.36rem;
						border-top: 0.02rem solid rgba(7, 17, 27, 0.1);
						display: flex;
						.liimg{
							display: block;
							width: 0.40rem;
							height: 0.40rem;
							padding-top: 0.28rem;
							padding-left: 0.24rem;
						}
						.huodonglitext{
		                	margin-left: 0.12rem;
		                	font-size: 0.24rem;
		                	line-height: 0.96rem;
		                }
					}
				}

			}
			.shijing{
				.shijingName{
					font-size:0.28rem; 
					font-weight: 700;
					padding-top: 0.36rem;
					padding-bottom: 0.16rem;
					margin-left: 0.36rem;
				};
	            .shijingImg{
	                margin: 0 0.36rem;
	                padding-bottom:0.36rem; 
	                height: 1.80rem;
	                overflow: hidden;
	                .imhul{
	                    display: flex;
