				};
				.huodongli{
					height: 0.96rem;
					margin:0 0.36rem;
					border-top:0.02rem solid rgba(7, 17, 27, 0.1);
					display: flex;
					.liimg{
						display: block;
						width: 0.40rem;
						height: 0.40rem;
						padding-top: 0.28rem;
						padding-left: 0.24rem;
					};
	                .huodonglitext{
	                	margin-left: 0.12rem;
