<template>
	<div class="goods">
		<food-detail></food-detail>
		<div class="menu-wrapper" ref = "menuWrapper">
			 <ul >
				   <li @click='selectMenu(k,$event)' v-for="(v,k) in goods" :key='k'  class="menu-list" :class = '{"current" : currentIndex === k}'>
                    <span class="text">{{v.name}}</span>
                </li>  
			</ul> 
		</div>
		<div class="goods-foods" ref = "menuWrapper1">
			<div>
				<div v-for="(v,k) in goods" :key='k' class="food-list food-list-hook">
					<span class="title">{{v.name}}</span>
					<div class="detail" v-for="(i,k) in (v.foods)" :key="k"  @click = 'showfood(i,$event)'>
						<div class="detailA">
							<div class="detailL">
								<img :src="i.image" alt="">
							</div>
							<div class="detaiR">
								<p class="foodname">{{i.name}}</p>
								<p class="foodtext">{{i.description}}</p>
								<div class="months"><span>月售{{i.sellCount}}份</span> <span>好评率:{{i.rating}}%</span></div>
								<div><span class="price1">￥{{i.price}}</span><span class="price2"></span></div>
								<div class="jiajian">
									<cartcontrol :food="i"></cartcontrol>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<shopcart></shopcart>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import foodDetail from '../detail/foodDetail.vue'
	import shopcart from '../Shopcart/shopcart.vue'
	import cartcontrol from '../Cartcontrol/Cartcontrol.vue'


	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				// console.log(this.calcHeight)
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},100)
			})
		},
       computed:{
            ...mapGetters([
				'goods'
		    ]),
		    currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },

		},
		methods : {
			showfood(f,e){
				if( !e._constructed ){
					return;
				};
				// console.log(7);
				this.$store.dispatch('showfood',f);
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.menuWrapper1.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.menuWrapper1.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	console.log(this.listHeight);
            },
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_initFood(){
				// probeType: 1 会截流,只有在滚动结束的时候派发一个 scroll 事件。2在手指 move 的时候也会实时派发 scroll 事件，不会截流。 3除了手指 move 的时候派发scroll事件，在 swipe（手指迅速滑动一小段距离）的情况下，列表会有一个长距离的滚动动画，这个滚动的动画过程中也会实时派发滚动事件
				this.foodBS = new BScroll(this.$refs.menuWrapper1,{
					probeType : 3,
					click:true

				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})
			},
		},
         components:{
	    // 'v-footer':Footer,
	     // addOrdelete,
	     // 'v-goodsdetail':goodsDetail
	     foodDetail,
	     shopcart,
	     cartcontrol
        }
	}

</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 1.1rem;
		width: 100%;
		display: flex;
		overflow: hidden;
		font-size:0.24rem;
		.menu-wrapper{
			flex:0 0 1.6rem;
			width: 1.6rem;
			overflow: hidden;
			.menu-list{
				height: 1.08rem;
				background: #f3f5f7;
				padding:0 0.24rem;
				display: table;
				&.current{
					background: rgba(7,17,27,0.1);
				}
				.text{
					border-bottom: 1px solid rgba(7,17,27,0.1);
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
				}
			}
		}
		.goods-foods{
			flex:1;
			width: 100%;
			.title{
				font-size: 0.24rem;
				color:rgb(147,153,159);
				line-height:0.52rem;
				height: 0.52rem;
				border-left:0.05rem solid #d9dde1;
				height:0.52rem;
				width: 100%;
				background-color: #f3f5f7;
				display:block;
				padding-left:0.28rem;
			}
			.detail{
				padding:0.36rem 0.36rem 0 0.36rem;
				.detailA{
					display:flex;
					border-bottom: 0.02rem solid rgba(7,17,27,0.1);
					padding-bottom:0.36rem;
					img{
						height: 1.36rem;
						width: 1.36rem;
					}
					
				}
				.detaiR{
					padding-left: 0.2rem;
					.foodname{
						font-size: 0.28rem;
						color: rgb(7, 17, 27);
						line-height: 0.28rem;
						padding-bottom: 0.16rem;
					}
					.foodtext{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.2rem;
						padding-bottom: 0.16rem;
					}
					.months{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.2rem;
						padding-bottom: 0.16rem;
					}
					.price1{
						font-size: 0.28rem;
						color: red;
						line-height: 0.48rem;
						font-weight: 700;
						padding-bottom: 0.36rem;
					}
					.price2{
						font-size: 0.2rem;
						color: rgb(147, 153, 159);
						line-height: 0.48rem;
						font-weight: 700;
						padding-bottom: 0.36rem;
						padding-left: 0.16rem;
					}
					.jiajian{
						position: relative;
						left:1.9rem;
						top: -0.5rem;
					}
				}

			}
		}
	}
</style>


