<template>
	<div class="header">
		<div class="bg">
			<img :src="seller.avatar">
		</div>
		<img :src="seller.avatar" alt="" class="avatar">
		<div class="content">
			<img src="../../imgs/brand.png" alt="" class="pinpai">
			<p class="title">{{ seller.name }}</p>
			<p class="text">{{ seller.description }} / {{ seller.deliveryTime }}分钟到达</p>
			<img src="../../imgs/decrease.png" alt="" class="jian">
			<span v-if="seller.supports" class="jianText">{{ seller.supports[0].description }}</span>
		</div>
		<div class="count"  @click='show'>
			5个&nbsp<i class="el-icon-arrow-right"></i>
		</div>
		<div class="foot">
			<img src="../../imgs/bulletin.png" class="bulletin">
			<span class="bulletinText">{{seller.bulletin}}</span>
			<i class="el-icon-arrow-right"></i>
		</div>
		<Detail></Detail>	
	</div>
</template>

<script>
	import { mapGetters } from "vuex"
	import Detail from "../detail/Detail.vue"

	export default {
		computed:{
			...mapGetters([
				"seller",
			])
		},
		components:{
			Detail
		},
		created(){
			this.$store.dispatch("getSeller");
		},
		methods:{
			show(){
				this.$store.dispatch("showdetail");
			}
		}
	}
</script>

<style lang="less" scoped>
	.header{
		height:2.68rem;
		background: grey;
		// z-index: 22;
		background: rgba(7, 17, 27, 0.5);
		width: 100%;
		position: relative;
		overflow: hidden;
		.bg{
			position: absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			z-index: -5;
			filter:blur(5px);
			img{
				width:100%;
				height: 100%;
			}
		}
		.avatar{
			width:1.28rem;
			height: 1.28rem;
			border-radius: 0.04rem;
			padding-top: 0.48rem;
			padding-left: 0.48rem;
			padding-bottom: 0.27rem;
		}
		.content{
			position: absolute;
			top: 0.52rem;
			left:2.08rem;
		}
		.pinpai{
			height:0.36rem;
		}
		.title{
			font-size: 0.32rem;
			color:rgb(255, 255, 255);
			font-weight: bold;
			line-height: 0.36rem;
			float: right;
			padding-left: 0.12rem;
		}
		.text{
			font-size: 0.24rem;
			color:rgb(255, 255, 255);
			font-weight: 200;
			line-height: 0.24rem;
			padding-top: 0.16rem;
		}
		.jian{
			height:0.24rem;
			padding-top: 0.2rem;
			padding-right: 0.08rem;
			vertical-align: bottom;
		}
		.jianText{
			font-size: 0.2rem;
			color:rgb(255, 255, 255);
			font-weight: 200;
			line-height: 0.24rem;
		}
		.count{
			height: 0.48rem;
			width: 0.9rem;
			background-color: rgba(0, 0, 0, 0.2);
			border-radius: 0.5rem;
			position: absolute;
			right:0.24rem;
			top:1.4rem;
			font-size: 0.2rem;
			color: rgb(255, 255, 255);
			text-align: center;
			font-weight: 200;
			line-height: 0.48rem;
		}
		.foot{
			height: 0.6rem;
			background: rgba(7, 17, 27, 0.2);
			display: flex;
			justify-content:center;
			align-items:center;
		}
		.bulletin{
			height: 0.3rem;
		}
		.bulletinText{
			width: 6.2rem;
			font-size: 0.2rem;
			color: rgb(255, 255, 255);
			font-weight: 200;
			line-height: 0.65rem;
			overflow: hidden;
			white-space: nowrap;
			text-overflow:ellipsis;
			padding-left: 0.08rem;			
		}
		.foot i{
			color: rgb(255, 255, 255);
			position: absolute;
			right:0.24rem;
			bottom:0.2rem;
		}
	}
</style>


