<template>
    <div class="shopcart">
        <div class="content">
            <div class="content-left">
                <div class="logo-wrap">
                    <div class="logo":class='{"highlight":totalCount>0}'@click="showList">
                        <i class="icon iconfont icon-gouwuche" :class='{"highlight":totalCount>0}'></i>
                    </div>
                    <div class="num" v-show='totalCount'>{{totalCount}}</div>
                </div>
                <div class="price">￥{{totalPrice}}</div>
                <div class="desc">另需配送费￥{{seller.deliveryPrice}}元</div>
            </div>
            <div class="content-righ" @click.stop.prevent = 'pay'>
                <div class="pay" :class='{"enough":totalPrice >= seller.minPrice}'>
                    {{payDesc}}
                </div>
            </div>
        </div>
        <!--购物车详情-->
        <transition name="movetop">
            <div class="shopcart-list" v-show="listShow">
                <div class="list-header">
                    <h1 class="title">购物车</h1 >
                    <span class="empty"@click="empty">清空</span>
                </div>
                <div class="list-content" ref="listWrapper">
                    <ul>
                        <li class="food" v-for="food in selectFoods">
                            <span class="name">{{food.name}}</span>
                            <div class="cartcoltrol-wrap">
                                <span class="price">￥{{food.price}}</span>
                                <cartcontrol :food='food' class="symbol"></cartcontrol>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
        <transition>
            <div class="list-mask" @click="showList" v-show="listShow"></div>
        </transition>
    </div>
</template>

<script>   
    import {mapGetters} from 'vuex'
    import cartcontrol from '../Cartcontrol/Cartcontrol.vue'
    export default {
        data(){
            return{
                // 购物车是否折叠
                fold:true
            }
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods',
                'food'
            ]),
            selectFoods(){
                let foods = [];
                this.goods.forEach( good => {
                    good.foods.forEach( food => {
                        if (food.count > 0){
                            foods.push(food);
                        }
                    }) 
                })
                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach(food => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach(food => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0 ){
                    return `￥${this.seller.minPrice}元起送`
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算"
                }

            },
            listShow(){
                if(!this.totalCount){
                    this.fold=true;
                    return false;
                }
                let show=!this.fold;
                return show;
            }
        },
        components :{
            cartcontrol
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        methods : {
            //显示购物车
           showList(){
            // console.log(1);
                if(!this.totalPrice){
                    return;
                }
                this.fold=!this.fold;
                if(!this.fold){
                    setTimeout( () => {
                        if( !this.foodDetailWrap ){
                            this.foodDetailWrap = new BScroll (this.$refs.listWrapper, {
                                click : true
                            });
                        }else{
                            this.foodDetailWrap.refresh();
                        }
                    },1000)
                }
            },
            //清空列表
            empty(){
                this.selectFoods.forEach((food)=>{
                    food.count=0;
                    this.flag=!this.flag;
                })
            },
            //去支付
            pay(){
                if(this.totalPrice >= this.seller.minPrice)
                    alert(this.totalPrice+this.seller.deliveryPrice);
            }

        }
    }

</script>

<style lang="less" type="text/less">
    .shopcart{
        height:0.96rem;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 50;
        .shopcart-list{
            position: fixed;
            left:0;
            bottom:0.98rem;
            max-height: 5.13rem;
            width: 100%;
            color: rgb(7, 17, 27);
            z-index:-11;
            background-color: #fff;
            .list-header{
                font-size:0.28rem;
                color: rgb(7, 17, 27);
                line-height: 0.8rem;
                background-color: #f3f5f7;
                display: flex;
                justify-content:space-between;
                padding:0 0.36rem;
                .title{
                    color: rgb(7, 17, 27);
                    font-size: 200;
                }
                .empty{
                    color: rgb(0, 160, 220);
                }
            }
            .list-content{
                padding:0 0.36rem;
                max-height: 4.3rem;
                overflow: hidden;
                .food{
                    display: flex;
                    padding:0.24rem 0;
                    justify-content:space-between;
                    border-bottom:1px solid rgba(7, 17, 27, 0.2);
                    .name{
                        font-size: 0.28rem;
                        line-height: 0.48rem;
                    }
                    .cartcoltrol-wrap{
                        display: flex;
                        .price{
                            font-size: 0.28rem;
                            color: rgb(240, 20, 20);
                            line-height: 0.48rem;
                            font-weight: 700;
                            margin-right: 1rem;  
                        }
                        .symbol{
                           margin-right: 0.2rem;
                        }
                    }
                }
            }
        }
        .movetop-enter-active,.movetop-leave-active{
            transition:all .5s
        }
        .movetop-enter,.movetop-leave-to{
            transform:translateY(100%);
        }
        .list-mask{
            position:fixed;
            top:0;
            left: 0;
            bottom: 0;
            right: 0;
            backdrop-filter: blur(10px);
            z-index: -12;
            background-color: rgba(7,17,27,0.6);
        }
        .content{
            display: flex;
            background: #141d27;
            // font-size: 0;
            .content-left{
                flex:1;   
                .logo-wrap{
                    display: inline-block;
                    vertical-align: top;
                    position: relative;
                    top: -0.2rem;
                    margin-left: 0.24rem;
                    margin-bottom: 0.04rem;
                    padding:0.12rem;
                    background: #141d27;
                    border-radius: 50%;
                    box-sizing: border-box;
                    .logo{
                        width: 0.88rem;
                        height: 0.88rem;
                        line-height: 0.88rem;
                        text-align: center;
                        background-color: #2b343c;
                        &.highlight{
                            background:#00a0dc;
                        }
                        border-radius: 50%;
                        i{
                            font-size: 0.46rem;
                            color:rgba(255,255,255,0.4);
                            &.highlight{
                                color: white;
                            }
                        }
                    }
                    .num{
                        position: absolute;
                        top:0;
                        right: 0;
                        width: 0.48rem;
                        height: 0.32rem;
                        line-height: 0.37rem;
                        text-align: center;
                        border-radius: 0.16rem;
                        font-size: 0.09rem;
                        font-weight: 700;
                        color:white;
                        background: rgb(240,20,20);
                        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
                    }
                }   
                .price{
                    display: inline-block;
                    font-size: 0.48rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color:#fff;
                    vertical-align: top;
                    margin-top: 0.24rem;
                    box-sizing: border-box;
                    padding-right: 0.14rem;
                    border-right: 1px solid rgba(255,255,255,0.1);
                    &.highlight{
                        color: #fff;
                    }
                }   
                .desc{
                    display: inline-block;
                    font-size: 0.30rem;
                    font-weight: 300;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.28rem;
                    box-sizing: border-box;
                    padding-left: 0.24rem;
                }           
            }
            .content-righ{
                flex:0 0 2.05rem;
                width: 2.05rem;
                .pay{
                    font-size: 0.24rem;
                    font-weight: 700;
                    line-height: 0.98rem;
                    height: 0.96rem;
                    color:#26333b;
                    background-color: #2b343c;
                    color:rgba(255,255,255,0.4);
                    text-align: center;
                    &.not-enough{
                        background: #2b333b;
                    }
                    &.enough{
                        background: #00a0dc;
                        color: #fff;
                    }
                }
            }

        }
    }
</style>



