<template>
    <div class="star" :class='sizeType'>
        <span v-for="active in itemlist" class="cc" :class='active'></span>
    </div>
</template>

<script>
    const LENGTH = 5;
    const ON = 'on';
    const HALF = 'half';
    const OFF = 'off';
    export default{
        props:['score','size'],

        computed:{
            sizeType(){
                return this.size;
            },
            itemlist(){
                let result = [];
                let integer = Math.floor(this.score);
                let decimal = this.score - integer;

                for(let i = 0 ; i < integer ; i++){
                    result.push(ON);
                };
                if(decimal　>= 0.5){
                    result.push(HALF);
                };
                while (result.length <　LENGTH){
                    result.push(OFF);
            };
            return result;
        }
    }
}
</script>

<style lang="less" scoped>
    .star{
        &.middle{
            .cc{
                display: inline-block;
                width: 0.36rem;
                height: 0.36rem;
                margin-right:0.12rem;
                &.on{
                    background-image: url('star_on.png');
                    background-size: 0.36rem 0.36rem;
                    background-repeat: no-repeat;
                }
                &.off{
                    background-image: url('star_off.png');
                    background-size: 0.36rem 0.36rem;
                    background-repeat: no-repeat;
                }
                &.half{
                    background-image: url('star_half.png');
                    background-size: 0.36rem 0.36rem;
                    background-repeat: no-repeat;
                }
            } 
        }
        &.small{
            .cc{
                display: inline-block;
                width: 0.24rem;
                height: 0.24rem;
                margin-right:0.12rem;
                &.on{
                    background-image: url('star_on.png');
                    background-size: 0.24rem 0.24rem;
                    background-repeat: no-repeat;
                }
                &.off{
                    background-image: url('star_off.png');
                    background-size: 0.24rem 0.24rem;
                    background-repeat: no-repeat;
                }
                &.half{
                    background-image: url('star_half.png');
                    background-size: 0.24rem 0.24rem;
                    background-repeat: no-repeat;
                }
            } 
        }
        .cc{
            display: inline-block;
            width: 0.48rem;
            height: 0.48rem;
            margin-right:0.12rem;
            &.on{
				background-image: url('star_on.png');
				background-size: 0.48rem 0.48rem;
				background-repeat: no-repeat;
			}
            &.off{
				background-image: url('star_off.png');
				background-size: 0.48rem 0.48rem;
				background-repeat: no-repeat;
			}
            &.half{
				background-image: url('star_half.png');
				background-size: 0.48rem 0.48rem;
				background-repeat: no-repeat;
			}
        }
    }
</style>



