import types from '../types.js'
import axios from 'axios'

const state={
    goods:[],
    countShow:false,
    countInit:0,
    foodshow : false,
    food : {}
}

const getters = {
    goods(state){
		return state.goods;
	},
    food(state){
        return state.food;
    },

    countShow(state){
        return state.countShow;
    },
    countInit(state){
        return state.countInit;
    },
    foodshow(state){
        return state.foodshow;
    }
}

const actions = {
    getGoods({commit}){

        axios.get('../../../static/data.json').then(resp=>{
            if(resp.data.errno === 0){
                commit(types.GET_GOODS,resp.data.goods)
            }
        });
    },
    showCount(context){
        context.commit(types.SHOW_COUNT);
        console.log(context);
    },
    showfood({commit},food){
        commit(types.SHOW_FOOD,food);
    },
    hidefood({commit}){
        commit(types.HIDE_FOOD);
    }
}

const mutations={
    [types.HIDE_FOOD](state){
        state.foodshow = false;
    },
    [types.SHOW_FOOD](state,food){
        state.foodshow = true;
        state.food = food;
    },
    [types.GET_GOODS](state,data){
        state.goods=data;
        console.log('aaaa');
    },
    [types.SHOW_COUNT](state){
        state.countShow = true;
        state.countInit++;
        // console.log(state.countInit);
    }
}

export default{
    state,
    getters,
    actions,
    mutations
}


