<template>
    <div class="ratings">
        <div class="head">
            <div class="headL">
                <ul>
                    <li>{{seller.score}}</li>
                    <li>综合评分</li>
                    <li>高于周边商家{{seller.rankRate}}%</li>
                </ul>
            </div>
            <div class="headline"></div>
            <div class="headR">
                <ul>
                    <li>
                        <span class="text">服务态度</span>
                        <Star :size="'middle'" :score='seller.serviceScore' class="star"></Star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </li>
                    <li>
                        <span class="text">食物口味</span>
                        <Star :size="'middle'" :score='seller.foodScore' class="star"></Star>
                        <span class="score">{{seller.foodScore}}</span>
                    </li>
                    <li>送达时间<span class="time">{{seller.deliveryTime}}分钟</span></li>
                </ul>
            </div>
        </div>
        <div class="line"></div>
        <div class="center">
            <div class="button">
                <el-button class="btn1" @click="getAll" :class="{'choosed':'getAll' === cc}">全部 <span>57</span></el-button>
                <el-button class="btn2" @click="ok" :class="{'ok':'ok' === cc}">满意 <span>47</span></el-button>
                <el-button class="btn3" @click="no" :class="{'no':'no' === cc}">不满意 <span>10</span></el-button>
            </div>
            <div class="line1"></div>
            <div class="check">
                <i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
                <span>只看有内容的评价</span>
            </div>
        </div>
        <div class="line2"></div>
        <div class="content">
            <div class="menu-wrapper" ref="menuWrapper">
                <ul>
                    <li v-for="(v,k) in ratLists" :key='k' class="menu-list" v-show="all || yes === v.rateType">
                        <!-- <span class="text">{{v.username}}</span> -->
                        <div class="rat">
                            <img :src="v.avatar" alt="" class='pic'>
                            <div class="ratR">
                                <span>{{v.username}}</span>
                                <span class="rateTime">{{v.rateTime | date}}</span>
                                <div class="star">
                                    <Star :score='v.score' :size="'small'"></Star><span class="time" v-if="v.deliveryTime>0">{{v.deliveryTime}}分钟送达</span>
                                </div>
                                <p class="text">{{v.text}}</p>
                                <div class="satisfy">
                                    <span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                    <span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
                                    <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
                                        <span>{{v}}</span>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </li>
                </ul>
            </div>

        </div>

    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import Star from '../star/star.vue'
    // import BScroll from 'better-scroll'

    export default {
        data() {
            return {
                all: true,
                yes: -1,
                flag: false,
                aa:false,
                cc:'getAll'
            }
        },
        components: {
            Star
        },
        filters:{
            date:function(value){
                function toDouble(n){
                    return n<10 ? "0" +n : n;
                }
                var d=new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
        computed: {
            ...mapGetters([
                'seller',
                'ratings'
            ]),
            ratLists() {
                if (this.flag) {
                    return this.ratings.filter((item) => {
                        return item.text
                    })
                }
                return this.ratings;
            }
        },
        created() {
            this.$store.dispatch('getRatings');
            this.$nextTick(() => {
                setTimeout(() => {
                    this._initMenu();
                }, 100)
            })
        },
        methods: {
            changeFlag() {
                this.flag = !this.flag
                this.refresh()
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh()
                }, 20)
            },
            _initMenu() {
                this.menuBS = new BScroll(this.$refs.menuWrapper);
            },
            ok() {
                this.yes = 0
                this.all = false
                this.refresh()
                this.cc = 'ok'
            },
            no() {
                this.yes = 1
                this.all = false
                this.refresh()
                this.cc = 'no'
            },
            getAll() {
                this.yes = -1
                this.all = true
                this.refresh()
            },
        },
    }

</script>

<style lang="less" scoped>
    .ratings {
        .head {
            display: flex;
            .headL {
                width: 2.75rem;
                text-align: center;
                ul {
                    margin-top: 0.36rem;
                    /* margin-bottom: 0.36rem; */
                    li:nth-of-type(1) {
                        font-size: 0.48rem;
                        color: rgb(255, 153, 0);
                        line-height: 0.56rem;
                    }

                    li:nth-of-type(2) {
                        font-size: 0.24rem;
                        color: rgb(7, 17, 27);
                        line-height: 0.24rem;
                        margin-top: 0.12rem;
                        margin-bottom: 0.16rem;
                    }
                    li:nth-of-type(3) {
                        font-size: 0.20rem;
                        color: rgb(147, 153, 159);
                        line-height: 0.2rem;
                        margin-bottom: 0.12rem;
                    }
                }
            }
            .headline {
                height: 1.4rem;
                width: 0.02rem;
                background: rgba(7, 17, 27, 0.1);
                margin-top: 0.36rem;
            }
            .headR {
                display: flex;
                width: 4.47rem;
                margin-left: 0.48rem;
                margin-right: 0.48rem;
                /* text-align: center; */
                ul {
                    margin-top: 0.36rem;
                    li {
                        display: flex;
                        color: rgb(7, 17, 27);
                        font-size: 0.24rem;
                        line-height: 0.36rem;
                        padding-bottom: 0.16rem;
                    }
                    /* span{
                            margin-left: 0.24rem;
                            margin-right: 0.24rem
                        }  */
                    .score {
                        color: rgb(255, 153, 0);
                        margin-left: 0.06rem;
                    }
                    .time {
                        color: rgb(147, 153, 159);
                        font-size: 0.24rem;
                        line-height: 0.36rem;
                        margin-left: 0.24rem;
                    }
                    .text {
                        margin-right: 0.24rem;
                    }
                }
            }
        }

        .line {
            height: 0.32rem;
            width: 100%;
            background: #f3f5f7;
            border-top: 1px solid rgba(7, 17, 27, 0.1);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        }

        .center {
            padding: 0.36rem 0.36rem 0 0.36rem;
            .button {
                margin-bottom: 0.36rem;
                .btn1 {
                    font-size: 0.24rem;
                    border-radius: 0.02rem;
                    line-height: 0.32rem;
                    border: 0;
                    color: white;
                    background: rgb(0, 160, 220);
                    &.choosed{
                        background:rgb(0, 160, 220);
                    }
                }
                .btn2 {
                    font-size: 0.24rem;
                    border-radius: 0.02rem;
                    line-height: 0.32rem;
                    border: 0;
                    color: rgb(77, 85, 93);
                    background: rgba(0, 160, 220, 0.2);
                    &.ok{
                        background: rgba(0, 160, 220,0.5);
                    }
                }
                .btn3 {
                    font-size: 0.24rem;
                    border-radius: 0.02rem;
                    line-height: 0.32rem;
                    border: 0;
                    color: rgb(77, 85, 93);
                    background: rgba(77, 85, 93, 0.2);
                    &.no{
                        background-color:rgba(77, 85, 93,0.5);
                        color:white;
                    }
                }
            }
            .line1 {
                height: 0.02rem;
                background: rgba(7, 17, 27, 0.1);
            }
        }
        .check {
            margin: 0.24rem 0;

            i {
                font-size: 0.48rem;
                color: rgb(147, 153, 159);
                line-height: 0.48rem;
                margin-right: 0.08rem;
            }
            .checked {
                color: rgb(0, 160, 220);
            }
            span {
                font-size: 0.24rem;
                color: rgb(147, 153, 159);
                line-height: 0.48rem;
            }
        }
        .line2 {
            height: 0.02rem;
            background: rgba(7, 17, 27, 0.1);
        }
        .content {
            .menu-wrapper {
                height: 4.5rem;
                overflow: hidden;
                .menu-list {
                    .rat {
                        margin: 0.36rem 0.36rem 0 0.36rem;
                        display: flex;
                        border-bottom:1px solid rgba(7, 17, 27, 0.1);
                        .pic {
                            border-radius: 50%;
                            width: 0.56rem;
                            height: 0.56rem;
                        }
                        .ratR {
                            margin-left: 0.24rem;
                            .rateTime{
                                font-size: 0.2rem;
                                font-weight: 200;
                                color: rgb(147, 153, 159);
                                line-height: 0.24rem;
                                position: absolute;
                                right:0.36rem;
                            }
                            .star {
                                display: flex;
                                margin: 0.08rem 0 0.12rem 0;
                                .time {
                                    font-size: 0.2rem;
                                    font-weight: 200;
                                    color: rgb(147, 153, 159);
                                    line-height: 0.48rem;
                                }
                            }
                            .text {
                                font-size: 0.24rem;
                                line-height: 0.36rem;
                                color: rgb(7, 17, 27);
                            }
                            .satisfy {
                                display: flex;
                                .good {
                                    color: rgb(0, 160, 220);
                                    font-size: 0.24rem;
                                    line-height: 0.84rem;
                                }
                                .bad {
                                    color: rgb(147, 153, 159);
                                    font-size: 0.24rem;
                                    line-height: 0.84rem;
                                }

                                .foods {
                                    display: flex;
                                    span {
                                        overflow: hidden;
                                        white-space: nowrap;
                                        text-overflow: ellipsis;
                                        margin: 0.16rem;
                                        border: 0.02rem solid rgba(7, 17, 27, 0.1);
                                        border-radius: 0.02rem;
                                        background-color: white;
                                        font-size: 0.18rem;
                                        color: rgb(147, 153, 159);
                                        padding:0.12rem ;
                                        width: 1rem;
                                        margin-bottom:0.36rem;

                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
</style>


