<template>
	<transition enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutLeft">	
		<div class="goodsdetail" v-show="foodshow" ref='imgWrapper'>
			<div class="detail-content">
				<div class="image-header">
					<img :src="food.image" alt="">
				</div>
				<div class="hide-food" @click = 'hidefood'>
					 <i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="basicinfo">
					<p class="foodinfoname">{{food.name}}</p>
					<p class="foodinfocount">月售{{food.sellCount}}份&nbsp;&nbsp;好评率{{food.rating}}%</p>
					<div class="foodinfoprice">
						<p><span class="yen">&yen;</span>{{food.price}}</p>
					</div>
					<div class="addcart" @click = 'addFirst( $event )' v-show = ' !food.count || food.count === 0 '>加入购物车</div>
					<div class="cc">
						<cartcontrol :food = 'food'></cartcontrol>
					</div>
				</div>
				<div class="middleline"></div>
				<div class="product-info">
					<p class="product-name">商品介绍</p>
					<div class="product-text">{{food.info}}</div>
				</div>
				<div class="middleline"></div>
			    <div class="btn-content">
			    	<p class="product-evaluate">商品评价</p>
			        <div class="btn">
				        <div class="btn1" @click="getAll">全部</div>
				        <div class="btn2" @click="ok">满意</div>
				        <div class="btn3" @click="no">不满意</div>
			        </div>
			        <div class="btn-text">
			          	<span class="el-icon-circle-check"  @click="changeFlag"></span>&nbsp;只查看有内容的评价
			        </div>
			    </div>

			    <div class="midline"></div>
			    <div class="ratingsinfo">
			        <ul>
				        <li v-for="v in ratLists" class="menu-li" v-show="all || yes === v.rateType">
				            <div class='headDiv'>
				              	<img :src="v.avatar" class="headImg">
				            </div>
				            <div class="rightdiv">
					            <p class="itemusername">{{v.username}}</p>
					            <p class="rateTime">{{v.rateTime | date}}</p>
				              	<div class="satisfy">
				                	<span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
				                	<span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
				                	<p class="itemtext">{{v.text}}</p>
				              	</div>
				            </div>
				        </li>
			        </ul> 
			    </div>
			</div>
			<shopcart></shopcart>
		</div>
	</transition>	
</template>
<script>
	import {mapGetters} from 'vuex'
	import Vue from 'vue'
	import cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	import shopcart from '../Shopcart/shopcart.vue'

	export default {
		data(){
			return{
				all:true,
				yes:-1,
				flag:false
			}
		},
		created(){
			this.$store.dispatch('getRatings');
			this.$nextTick(()=>{  
	            this._initFood();
	        })
		},
		components:{
			cartcontrol,
			shopcart
		},
		filters:{
            date:function(value){
                function toDouble(n){
                    return n<10 ? "0" +n : n;
                }
                var d=new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
		computed:{
			...mapGetters([
				'foodshow',
				'food'
			]),
			ratLists(){
				if(this.flag){
					return this.food.ratings.filter((item)=>{
						return item.text
					})
				}
				return this.food.ratings;
			}
		},
		// watch:{
		// 	food(){
		// 		this.$nextTick(()=>{
		//           this._initFood();
		//         })  
		// 	}
		// },
		methods:{
			addFirst(e){
                // console.log(4)
                if( !e._constructed ){
                    return;
                }
                Vue.set( this.food , 'count' , 1 );
            },
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			_initMenu() {
                this.menuBS = new BScroll(this.$refs.imgWrapper,{click:true});
            },
            changeFlag() {
                this.flag = !this.flag;
                this.refresh()
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh()
                }, 20)
            },
            ok() {
                this.yes = 0
                this.all = false
                this.refresh()
            },
            no() {
                this.yes = 1
                this.all = false
                this.refresh()
            },
            getAll() {
                this.yes = -1
                this.all = true
                this.refresh()
            },
		},
		// watch:{
		// 	food(){
		// 		this.$nextTick(()=>{
		// 			this._initMenu();
		// 		})
		// 	}
		// },
		watch:{
			food(){
				setTimeout( () => {
		            if( !this.menuBS ){
		            	this.menuBS = new BScroll (this.$refs.imgWrapper, {
		            		click : true
		            	})
		            }else{
		            	this.menuBS.refresh();
		            }
				},200)
			}
		},
		created(){
			this.$store.dispatch('getRatings');
			// this.$nextTick(() => {
   //              setTimeout(() => {
   //                  this._initMenu();
   //              },100)    
   //          });
		}
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		z-index: 999;
		left: 0;
		right: 0;
		bottom: 0;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom:100%;
				img{
					position:absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.hide-food{
				position: fixed;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				padding:10px;
				color:white;
			}
			.basicinfo{
				padding-top: 0.36rem;
				padding-left: 0.36rem;
				position: relative;
				// border: 1px solid #000;
				.foodinfoname{
					font-size: 0.28rem;
					font-weight: 700;
					color: rgb(7, 17, 27);
					line-height: 0.28rem;
				}
				.foodinfocount{
					font-size: 0.2rem;
					color: rgb(147, 153, 159);
					line-height: 0.2rem;
					padding-top: 0.16rem;
					padding-right: 0.24rem;
				}
				.foodinfoprice{
					font-weight: 700;
					font-size: 0.32rem;
					color: rgb(240, 20, 20);
					line-height: 0.48rem;
					padding: 0.36rem 0; 
				}
				.addcart{
					position: absolute;
					width: 1.48rem;
					height: 0.5rem;
					line-height: 0.5rem;
					border-radius: 0.24rem;
					background: #00ccff;
					color: white;
					text-align: center;
					right: 0.2rem;
					bottom: 0.4rem;
					z-index: 999;
				}
				.cc{
					position: absolute;
					line-height: 0.48rem;
					width: 100px;
					height: 30px;
					bottom: 0.3rem;
					right: -0.2rem;
				}
			}
			.middleline{
        	width: 100%;
        	height: 0.29rem;
        	background-color: rgba(7, 17, 27, 0.1);
        	}
        	.product-info{

        		.product-name{
        			margin-left: 0.36rem;
        			padding-top: 0.36rem;
        			font-size: 0.28rem;
        			font-weight: 700;
        			color: rgb(7, 17, 27);
        			line-height: 0.28rem;
        		}
        		.product-text{
        			font-size: 0.24rem;
        			padding-top: 0.12rem;
        			padding-bottom: 0.36rem;
        			font-weight: 200;
        			color: rgb(77, 85, 93);
        			line-height: 0.48rem;
        			margin-left: 0.52rem;
        			width: 6.5rem;
        		}
        	}
        	.btn-content{
		        padding:0.36rem 0.36rem 0.32rem 0.36rem;
		        border-bottom:0.02rem solid rgba(0,0,0,0.1);
	          	.product-evaluate{
	        		font-size: 0.28rem;
	        		font-weight: 700;
	        		color: rgb(7, 17, 27);
	        		line-height: 0.28rem;
	          	}
		        .btn{
			        width:100%;
			        height:1rem;
			        display: flex;
			        border-bottom:0.02rem solid rgba(0,0,0,0.1);
		          .btn1{
			            width:1.2rem;
			            height:0.5rem;
			            margin-top:0.1rem;
			            font-size:0.24rem;
			            background-color:#3399ff;
			            color: white;
			            text-align:center;
			            line-height: 0.5rem;
		         	}
		          .btn2{
			            width:1.2rem;
			            height:0.5rem;
			            font-size:0.24rem;
			            background-color:#99ccff;
			            margin-top:0.1rem;
			            color:white;
			            text-align:center;
			            line-height: 0.5rem;
			            margin-left:0.18rem;
			        }

		          	.btn3{
			            width:1.2rem;
			            height:0.5rem;
			            font-size:0.24rem;
			            background-color:#ccc;
			            margin-top:0.1rem;
			            color:white;
			            text-align:center;
			            line-height: 0.5rem;
			            margin-left:0.2rem;
			        }
	        	}
		        .btn-text{
		            height: 0.8rem;
		            line-height:1.2rem;
		            font-size:0.24rem;
		            color: rgb(147, 153, 159);
		            font-weight:600;
		            .el-icon-circle-check{
			            font-size: 0.35rem;
			            display:inline-block;
			            vertical-align:middle;
			            color: rgb(147, 153, 159);
		            }
		            .checked{
		              	color:rgb(0, 160, 220);
		            }
		        }
        	}
        	.ratingsinfo{
				.menu-li{
					height: 2.2rem;
					margin: 0 0.36rem;
					border-bottom:1px solid rgba(7, 17, 27, 0.1);
					display: flex;
					.headDiv{
						height: 2.0rem;
                        .headImg{
                        	display: block;
                        	border-radius: 50%;
	                        width: 0.56rem;
	                        height: 0.56rem;
	                        position: absolute;
                            right:0.36rem;
                            margin-top: 0.32rem;
                        }
					}
					.rightdiv{
						margin-left: 0.24rem;
						.itemusername{
							font-size: 0.2rem;
                            font-weight: 200;
                            color: rgb(147, 153, 159);
                            line-height: 0.24rem;
                            position: absolute;
                            right:1.1rem;
                            margin-top: 0.5rem;
						}
						.rateTime{
							font-size: 0.2rem;
                            font-weight: 200;
                            color: rgb(147, 153, 159);
                            line-height: 0.24rem;
                            margin-top: 0.5rem;
						}
						.satisfy{
							display: flex;
							.good {
                                color: rgb(0, 160, 220);
                                font-size: 0.24rem;
                                line-height: 0.84rem;
                                padding-right:0.1rem; 
                            }
                            .bad {
                                color: rgb(147, 153, 159);
                                font-size: 0.24rem;
                                line-height: 0.84rem;
                                padding-right:0.1rem; 
                            }
                            .itemtext{
								font-size: 0.24rem;
	                            font-weight: 200;
	                            color: rgb(7, 17, 27);
	                            line-height: 0.32rem;
	                            padding-top: 0.25rem;
						    }
						}
					}
				}
        	}
		}
	}
</style>


