<template>
<transition enter-active-class="animated fadeInDown" leave-active-class="animated fadeOutDown">
	<div class="detail" v-show='detailshow'>
		<p class="title">{{ seller.name }}</p>
		<div class="stars">
			<img src="../../imgs/star_on.png" class="star">
			<img src="../../imgs/star_on.png" class="star">
			<img src="../../imgs/star_on.png" class="star">
			<img src="../../imgs/star_on.png" class="star">
			<img src="../../imgs/star_on.png" class="star">
		</div>
		<div class="line1"></div>
		<p class="msg1">优惠信息</p>
		<div class="line2"></div>
		<ul v-if="seller.supports" class="supports">
			<li class="support-item" v-for="item in seller.supports">
				<span class="pic" :class="iconClassMap[item.type]"></span>
				<span class="txt">{{item.description}}</span>
			</li>
		</ul>
		<div class="line3"></div>
		<p class="msg2">商家公告</p>
		<div class="line4"></div>
		<p class="text">{{seller.bulletin}}</p>
		<i class="el-icon-close icon"  @click='close'></i>
	</div>
</transition>
</template>

<script>
	import {mapGetters} from 'vuex'

	export default {
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			])
		},
		created(){
			this.iconClassMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
		},
		methods : {
			close(){
				this.$store.dispatch('closedetail')
			}
		}
	}
</script>

<style lang='less' scoped>
	.detail{
		position: fixed;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		background:rgba(7, 17, 27, 0.8);
		z-index: 99;
		// filter:blur(5px);
		.title{
			font-size: 0.32rem;
			font-weight: 700;
			color:rgb(255, 255, 255);
			line-height: 0.32rem;
			padding-top: 1.28rem;
			padding-left: 2.5rem;
			padding-bottom:0.32rem; 
		}
		.stars{
			padding-left: 1.6rem;
			padding-bottom: 0.56rem;
			.star{
				height:0.48rem;
				padding-right:0.4rem; 
			}
		}
		.line1{
			height: 0.03rem;
			width: 2.24rem;
			background: rgba(255, 255, 255, 0.2);
			position: absolute;
			left:0.72rem;
		}
		.msg1{
			font-size: 0.28rem;
			font-weight: 700;
			color: rgb(255, 255, 255);
			line-height: 0.28rem;
			position: absolute;
			left: 3.2rem;
			top:2.85rem;
		}
		.line2{
			height: 0.03rem;
			width: 2.24rem;
			background: rgba(255, 255, 255, 0.2);
			position: absolute;
			right:0.72rem;
		}
		.supports{
			padding-left: 1rem;
			padding-top: 0.48rem;
			padding-bottom: 0.56rem;
			.pic{
				display: inline-block;
				vertical-align: top;
				width: 0.32rem;
				height: 0.32rem;
				// padding-bottom: 0.24rem;
				background-size: 0.32rem 0.32rem;
				&.decrease{
					background-image: url("../../imgs/decrease.png");
				}
				&.discount{
					background-image: url("../../imgs/discount.png");
				}
				&.special{
					background-image: url("../../imgs/special.png");
				}
				&.invoice{
					background-image: url("../../imgs/invoice.png");
				}
				&.guarantee{
					background-image: url("../../imgs/guarantee.png");
				}
			}
			.txt{
				font-size: 0.24rem;
				font-weight: 200;
				color: rgb(255, 255, 255);
				line-height: 0.24rem;
				padding-left: 0.12rem;
			}
		}
		.line3{
			height: 0.03rem;
			width: 2.24rem;
			background: rgba(255, 255, 255, 0.2);
			position: absolute;
			left:0.72rem;
		}
		.msg2{
			font-size: 0.28rem;
			font-weight: 700;
			color: rgb(255, 255, 255);
			line-height: 0.28rem;
			position: absolute;
			left: 3.2rem;
			top:5.5rem;
		}
		.line4{
			height: 0.03rem;
			width: 2.24rem;
			background: rgba(255, 255, 255, 0.2);
			position: absolute;
			right:0.72rem;
		}
		.text{
			width: 5.55rem;
			font-size: 0.24rem;
			font-weight: 200;
			color:rgb(255, 255, 255);
			line-height: 0.48rem;
			padding-left: 0.96rem;
			padding-top: 0.48rem;
		}
		.icon{
			font-size: 0.64rem;
			color: rgba(255, 255, 255, 0.5);
			position: absolute;
			bottom: 0.64rem;
			left:3.5rem;
		}
	}
</style>


